.banner {
   background: #FAFBFC;
   padding: 15px;
   font-size: 14px;
   text-align: center;
   animation-name: appear;
   animation-duration: 1.5s;
   // z-index: 0;
   // margin-top: 40px;
   @keyframes appear {
      from {
         opacity: 0;
      }

      to {
         opacity: 1;
      }
   }

   .bold {
      font-weight: 600;
   }

   .light-link {
      color: #999;
      transition: 0.2s all;
      text-decoration: none;
   }

   .link-link:hover {
      color: blue;
   }
}
